@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
* {
  margin: 0;
  padding: 0;
}

.no-match{
  font-size: 32px;
  text-align: center;
  
}


/* Scrollbars */

::-webkit-scrollbar {
  height: 5px;
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #666; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #ddd; 
}

/* Errors n stuff */
.loading-txt{
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: 'Poppins', sans-serif;
  text-align: center;
  font-size: 32px;
  font-weight: 800;
}
ul {
    list-style: none;
}

a {
    text-decoration: none;
    color: inherit;
}

.headerbox {
    width: 100%;
    height: 60px;
    background-color: #2979ff;
    font-family: "Poppins", sans-serif;
}

.header-logo {
    height: 50px;
    width: 50px;
    background-image: url(/static/media/applogo512.eed23e5c.png);
    background-size: contain;
    float: left;
    margin: 5px;
    cursor: pointer;
    -webkit-transition: all 0.15s ease;
    transition: all 0.15s ease;
}

.header-logo:hover {
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.li-content {
    padding-left: 10px;
    padding-right: 10px;
    color: white;
    position: relative;
    top: calc(50% - 12px);
}

.header-left-ul {
    height: 60px;
    float: left;
}

.header-li {
    display: inline-block;
    margin-left: 10px;
    height: 60px;
    -webkit-transition: all 0.15s ease;
    transition: all 0.15s ease;
}

.header-li:hover {
    background-color: rgb(32, 94, 201);
    -webkit-transition: all 0.15s ease;
    transition: all 0.15s ease;
}

.header-right-ul {
    height: 60px;
    float: right;
    margin-right: 24px;
}


/*################################################
Jumbotron
*/

.jumbotron {
    width: 100%;
    height: calc(100vh - 60px);
    position: absolute;
    z-index: -1;
    background-color: white;
}

#canvas {
    width: 100%;
    height: 100%;
    display: block;
    background-color: white;
    position: relative; 
}

.above-jumbo {
    font-family: "Poppins", sans-serif;
    width: 100%;
    height: calc(100vh - 60px - 24px);
    
}

.above-jumbo .landing-main-txt {
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.main-header {
    font-size: 48px;
    color: #eee;
    cursor: pointer;
    padding: 15px;
}

/* 
Footer
*/

