.dot, .dot:after {
  display: inline-block;
  width: 2em;
  height: 2em;
  border-radius: 50%;
  -webkit-animation: a 1.5s calc(((var(--i) + 0)/var(--n) - 1)*1.5s) infinite;
          animation: a 1.5s calc(((var(--i) + 0)/var(--n) - 1)*1.5s) infinite;
  -webkit-animation: a 1.5s calc(((var(--i) + var(--o, 0))/var(--n) - 1)*1.5s) infinite;
          animation: a 1.5s calc(((var(--i) + var(--o, 0))/var(--n) - 1)*1.5s) infinite;
}
.dot:after {
  --o: 1;
  background: white;
  content: "";
}

